import './App.css';
import React from 'react';
import { Routes ,Route } from 'react-router-dom';
import 'bootstrap/dist/css/bootstrap.min.css';
import SiteMenu from './sitemenu';
import Footer from './footer';
import AboutUs from './AboutUs';

function App() {
  return (
    <div style={{width:1000, marginLeft:100, marginTop:100}} className="App">

      <Routes>
        <Route exact path='/' component={App} />
        <Route exact path='/aboutus' component={AboutUs} />
      </Routes>

     <h2>Welcome to Page Routing Demo in React</h2>
        <hr/>
        <SiteMenu/>
        <hr/>
     <div>
        <p>
            React Router is a fully-featured client and server-side routing library for React, 
            a JavaScript library for building user interfaces. React Router runs anywhere React runs; 
            on the web, on the server with node.js, and on React Native.
        </p>
        <img style={{width:880,height:300}} src='../images/React-Learning-Path-1.png' alt=''/>
      </div>
      <hr/>
      <Footer/>
    </div>
  );
}

export default App;
