import React from "react";
import Button from 'react-bootstrap/Button';

const SiteMenu =()=>{
    return(
        <div> 
        <span >
          <Button as="a" variant="secondary">Home</Button>
        </span>

        <span style={{marginLeft:20}}> 
          <Button as="a" variant="secondary">About Routing</Button>
        </span> 

        <span style={{marginLeft:20}}> 
          <Button as="a" variant="secondary">Contact us</Button>
        </span> 
      </div>
    );
}

export default SiteMenu;